import React from 'react'
import s from './MySelect.module.scss';

type IMySelect = {
    onChange: (e:React.ChangeEvent<HTMLSelectElement>) => void
    className?: string
    defaultValue: string
    data?: any[]
    children?: JSX.Element | JSX.Element[] 
}

export default function MySelect({ onChange, className, defaultValue, data, children }: IMySelect) {
    return (
        <select
            className={`${s.select} ${className}`}
            onChange={onChange}>
            <option value={defaultValue}>{defaultValue}</option>
            {data?.map((el:string, index:number) => {
                const renamedValue = () => {
                    switch (el) {
                        case 'female': return ''
                        case 'male': return ''
                        case 'unisex': return ''
                        case 'novaya_s_birkoy': return '  '
                        case 'novaya_bez_birki': return '  '
                        case 'nebolshie_defekti': return ' '
                        case 'nadevalas_odin_raz': return '  '
                        case 'nadevalas_neskolko_raz': return '  '
                        case 'top': return ''
                        case 'bottom': return ''
                        case 'shoes': return ''
                        case 'accessories': return ''                         
                        default: return el
                    }
                }
                return(
                    <option key={index} value={el}>{renamedValue()}</option>
                )
            })}
            {children}
        </select>
    )
}